import React, { Component } from 'react';
import { observer, inject } from 'mobx-react'
import Config from '../../../contract/config.json';
import * as ethers from 'ethers/index.js';
import queryString from 'query-string';
import QRCode from 'qrcode-react';

@inject('ethereumStore')
@observer
export default class VotingProject extends Component {
    constructor(props) {
        super(props);
        let { query } = queryString.parseUrl(window.location.hash.substr(1));

        this.state = {
            title: this.props.voting.object.title,
            candidates: this.props.voting.object.candidates || [],
            votingId: this.props.voting.object.votingId || query.votingId,
            totalVotes: []
        };
        this.provider = this.props.ethereumStore.provider;
        this.contract = new ethers.Contract(Config.voting[3].contractAddress, Config.voting.abi, this.provider)
    }

    componentWillMount = () => {
        this.getInfo()
        this.interval = setInterval(() => {
            this.getInfo();
        }, 4000)
    }
    componentWillUnmount = () => {
        clearInterval(this.interval)
    }

    getInfo = async () => {
        let candidates = this.state.candidates.length ? this.state.candidates : await this.contract.candidates(this.state.votingId);
        let title = this.state.title || await this.contract.getTitle(this.state.votingId);
        let totalVotes = await this.contract.totalVotes(this.state.votingId);
        totalVotes = totalVotes.map(v => v.toNumber())

        this.setState({
            candidates,
            totalVotes,
            title
        })
    }

    voteForCandidate = async (name) => {
        let signer = this.contract.connect(this.props.ethereumStore.wallet.connect(this.provider));
        let tx = await signer.voteForCandidate(this.state.votingId, name);
        alert('txid: ' + tx.hash);
        await tx.wait();
        this.getInfo();
    }

    render() {
        let candidates = this.state.candidates.map((v, i) => {
            return (
                <tr key={v}>
                    <td>{i+1}</td>
                    <td>{v}</td>
                    <td>{this.state.totalVotes[i]}</td>
                    <td><button onClick={() => this.voteForCandidate(v)}>投我一票</button></td>
                </tr>
            )
        })
        return (
            <div className="col-xs-12 col-sm-6 col-lg-3" key={this.state.votingId}>
                <div className="box box-primary">
                    <div className="box-header">
                        {this.state.title}
                    </div>
                    <div className="box-body">
                        <label className="control-label">選項</label>
                        <table className="table table-striped">
                            <thead>
                                <tr>
                                    <th>#</th>
                                    <th>Name</th>
                                    <th>Votes</th>
                                    <th>Action</th>
                                </tr>
                            </thead>
                            <tbody>
                                {candidates}
                            </tbody>
                        </table>
                    </div>
                    <div className="box-footer">
                        <label className="control-label">投票QRCode</label>
                        <div className="form-group">
                            <QRCode size={200} value={window.location.href} />
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
export { VotingProject };